body {
  background-color: #d5dde3;
}
.name {
  font-size: 1.6em;
  color: #141a25;
  font-weight: bold;
  font-family: "Courier New", Courier, monospace;
  margin-bottom: 0px;
}

.judul {
  font-size: 1.6em;
  color: #703c5c;
  font-weight: bold;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  margin-bottom: 0px;
}

.card-deck.card-decks {
  margin-top: 35px;
}

.corona-body {
  margin-top: 0px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: auto;
  height: 60vh;
  width: 60vw;
  background-color: #d5dde3;
}
.confirm {
  background-color: #938e94;
  border: 1 px solid #3a4b5b;
}
.c-title {
  font-size: 1.3em;
  font-weight: bold;
  color: #d5dde3;
  font-family: "Courier New", Courier, monospace;
  margin-top: 15px;
  margin-bottom: 0px;
}
.c-body {
  font-weight: bold;
  font-size: 10px;
}

.recovered {
  background-color: #b0a295;
  border: 1 px solid #3a4b5b;
}
.box {
  border-radius: 20px;
  box-shadow: 8px 8px 8px #141a25;
}
.card-decks {
  margin-top: 4em;
}

.deaths {
  background-color: #5d5c61;
  border: 1 px solid #3a4b5b;
}

.lastupdate {
  font-size: 0.9em;
  color: #703c5c;
  margin-bottom: 35px;
}
.number {
  font-size: 3em;
}

button {
  padding: 10px 20px;
  color: white;
  border-radius: 5px;
  border: none;
  font-weight: bold;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

button[type="keporto"] {
  background-color: #557a95;
  border-bottom: solid 2px #3d617a;
  margin-bottom: 40px;
}
